//React
import React, { useState } from 'react';
import './index.css';

//components
import Header from '../Header';

function Feedback() {

  const memberId = window.sessionStorage.getItem('member_id');
  const [workout, setWorkout] = useState('');
  const [eat, setEat] = useState(formatNumber(Number(window.sessionStorage.getItem('kcal'))));
  const [result, setResult] = useState('');

  const handleWorkoutKcalChange = (e) => {
    const inputValue = e.target.value;
    const numericValue = inputValue.replace(/[^0-9]/g, '');
    setWorkout(numericValue);
  };

  const handleFeedbackButton1Click = () => {
    const feedback1 = document.getElementById('feedback-1');
    const feedback2 = document.getElementById('feedback-2');
    feedback1.style.display = 'none';
    feedback2.style.display = 'flex';
  };

  const handleFeedbackButton2Click = async (event) => {
    event.preventDefault();

    const formData = new FormData();
    formData.append('member_id', memberId);
    formData.append('used_calories', Number(workout));
    formData.append('food_calories', Number(eat));

    function generateBoundary() {
      return '----WebKitFormBoundary' + Math.random().toString(36).substring(2);
    }

    function formatFormData(formData, boundary) {
      const body = [];

      for (const [key, value] of formData.entries()) {
        body.push(`--${boundary}`);
        body.push(`Content-Disposition: form-data; name="${key}"`);
        body.push('');
        body.push(value);
      }

      body.push(`--${boundary}--`);

      return body.join('\r\n');
    }

    const boundary = generateBoundary();

    try {
      const response = await fetch('http://3.34.122.27:8080/foods/feedback/', {
        method: 'POST',
        headers: {
          'Content-Type': `multipart/form-data; boundary=${boundary}`
        },
        body: formatFormData(formData, boundary)
      });

      if (response.ok) {
        const data = await response.json();
        setResult(data.data);
        const feedback2 = document.getElementById('feedback-2');
        const feedback3 = document.getElementById('feedback-3');
        feedback2.style.display = 'none';
        feedback3.style.display = 'flex';
      } else {
        alert('  .');
      }
    } catch (error) {
      alert(' .  .');
    }
  };

  function formatNumber(value) {
    if (Number.isInteger(value)) {
      return value;
    } else if (typeof value === 'number') {
      return value.toFixed(1);
    } else {
      return value;
    }
  }

  return (
    <>
      <Header />
      <div class="feedbackContainer">
        <div id="feedback-1">
          <p class="feedbackTitle">Feedback</p>
          <p class="feedbackSubtitle">       !</p>
          <button class="feedbackHomeButton" onClick={handleFeedbackButton1Click}></button>
        </div>

        <div id="feedback-2">
          <div class="feedbackRow">
            <p class="feedbackText">  </p>
            <input
              type="text"
              value={workout}
              onChange={handleWorkoutKcalChange}
            />
            <p class="feedbackTextSmall">kcal</p>
          </div>
          <div class="feedbackRow">
            <p class="feedbackText"> </p>
            <input
              type="text"
              value={eat}
              readOnly
              style={{ fontWeight: '700', cursor: 'default'}}
            />
            <p class="feedbackTextSmall">kcal</p>
          </div>
          <button class="feedbackHomeButton" onClick={handleFeedbackButton2Click} style={{ background: '#518EFF', marginTop: 80 }}> !</button>
        </div>

        <div id="feedback-3">
          <p class="feedbackTextLarge"> !</p>
          <div class="resultBox">
            <div class="resultRow">
              <p class="feedbackCategory">  </p>
              <p class="feedbackTextMedium">{result.food_calories}&nbsp;kcal</p>
            </div>
            <div class="resultRow">
              <p class="feedbackCategory"> </p>
              <p class="feedbackTextMedium">{formatNumber(result.base_calories)}&nbsp;kcal</p>
            </div>
            <div class="resultRow">
              <p class="feedbackCategory">  </p>
              <p class="feedbackTextMedium">{result.used_calories}&nbsp;kcal</p>
            </div>
          </div>
          <p class="feedbackTextMedium">{result.status === 'much' ? '' : ''} <span class="feedbackTextLarge">{result.calories} kcal</span>!</p>
          {
            result.status === 'much'
              ? <p class="feedbackTextMedium" style={{ color: '#9C9C9C' }}>       </p>
              : <p class="feedbackTextMedium" style={{ color: '#9C9C9C' }}>        </p>
          }
          {
            result.status === 'much'
              ? <div class="recommandBox">
                <p class="feedbackTextMedium"> {result.squat_m} </p>
                <p class="feedbackTextMedium"> {result.bike_m} </p>
                <p class="feedbackTextMedium"> {result.pushup_m} </p>
                <p class="feedbackTextMedium"> {result.swim_m} </p>
                <p class="feedbackTextMedium"> {result.run_m} </p>
              </div>
              : <div class="recommandBox">
                <p class="feedbackTextMedium"> {result.potato_ea} </p>
                <p class="feedbackTextMedium">  {result.egg_ea} </p>
                <p class="feedbackTextMedium"> {result.chest_ea} </p>
                <p class="feedbackTextMedium"> {result.banana_ea} </p>
              </div>
          }
        </div>
      </div>
    </>
  );
}

export default Feedback;
