//React
import React from 'react';
import { useNavigate } from 'react-router-dom';
import './index.css';

//components
import Header from '../Header';

//Assets
import landing from '../../assets/landing.jpeg';

function Landing() {

  const navigate = useNavigate();
  const sessionUserData = window.sessionStorage.getItem('member_id');

  const handleHomeButtonClick = () => {
    if (sessionUserData) {
      navigate('/home');
    } else {
      navigate('/login');
    }
  };

  return (
    <>
      <Header />
      <div class="landingContainer">
        <img class="landingImage" src={landing} alt=" " />
        <div class="landingColumn">
          <p class="landingTitle">"   !"</p>
          <p class="landingSubtitle"> Personal training  <br />    ?</p>
          <p class="landingSubtitle">      ?</p>
          <button class="landingHomeButton" onClick={handleHomeButtonClick}> !</button>
        </div>
      </div>
    </>
  );
}

export default Landing;
