//React
import React from 'react';
import { useNavigate } from 'react-router-dom';
import './index.css';

//Assets
import logo from '../../assets/logo.png';
import profileMan from '../../assets/profile_man.jpeg';
import profileWoman from '../../assets/profile_woman.jpeg';

function Header() {

  const navigate = useNavigate();
  const sessionId = window.sessionStorage.getItem('member_id');
  const sessionName = window.sessionStorage.getItem('name');
  const sessionGender = window.sessionStorage.getItem('gender');

  const handleLogoClick = () => {
    navigate('/');
  };

  const handleLoginButtonClick = () => {
    navigate('/login');
  };

  return (
    <div class="header">
      <img class="logo" src={logo} alt="" onClick={handleLogoClick}/>
      {
        sessionId
          ? <div class="headerColumn">
            <img class="profile" src={sessionGender===''?profileMan:profileWoman} alt="" />
            <p>{sessionName}</p>
          </div>
          : <button class="login-button" onClick={handleLoginButtonClick}></button>
      }
    </div>
  );
}

export default Header;
